import * as React from "react";
import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Grid from "@mui/material/Grid";

const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === "dark" ? "#19232E" : "#19232E",
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: "center",
    color: "#fff",
}));

export default function BasicSelect() {
    const [launguage, setLaunguage] = React.useState("Tr");
    const [memory, setMemory] = React.useState(4096);
    const [windowSize, setWindowSize] = React.useState(0);
    const [version, setVersion] = React.useState(0);

    const handleLaunguageChange = (event) => {
        setLaunguage(event.target.value);
    };
    const handleMemoryChange = (event) => {
        setMemory(event.target.value);
    };
    const handleWindowSizeChange = (event) => {
        setWindowSize(event.target.value);
    };
    const handleVersionChange = (event) => {
        setVersion(event.target.value);
    };

    return (
        <div>
            <Box sx={{ flexGrow: 1 }}>
                <Grid container spacing={2}>
                    <Grid item xs={12} md={12} lg={12}>
                        <h2>LAUNCHER AYARLARI</h2>
                    </Grid>
                    <Grid item xs={6}>
                        <Item>
                            <FormControl fullWidth>
                                <InputLabel sx={{ color: "#fff !important" }} id="launguage-select-label">
                                    Dil
                                </InputLabel>
                                <Select sx={{ color: "#fff !important" }} labelId="launguage-select-label" id="launguage-select" value={launguage} label="Launguage" onChange={handleLaunguageChange}>
                                    <MenuItem value={"Tr"}>Trke</MenuItem>
                                    <MenuItem value={"En"}>ngilizce</MenuItem>
                                </Select>
                            </FormControl>
                        </Item>
                    </Grid>
                    <Grid item xs={6}>
                        <Item>
                            <FormControl fullWidth>
                                <InputLabel id="memory-select-label" sx={{ color: "#fff !important" }}>
                                    Bellek
                                </InputLabel>
                                <Select sx={{ color: "#fff !important" }} labelId="memory-select-label" id="memory-select" value={memory} label="Memory" onChange={handleMemoryChange}>
                                    <MenuItem value={4096}>4096 MB</MenuItem>
                                    <MenuItem value={2048}>2048 MB</MenuItem>
                                    <MenuItem value={1024}>1024 MB</MenuItem>
                                </Select>
                            </FormControl>
                        </Item>
                    </Grid>
                    <Grid item xs={6}>
                        <Item>
                            <FormControl fullWidth>
                                <InputLabel sx={{ color: "#fff !important" }} id="windowSize-select-label">
                                    Ekran Boyutu
                                </InputLabel>
                                <Select sx={{ color: "#fff !important" }} labelId="windowSize-select-label" id="windowSize-select" value={windowSize} label="windowSize" onChange={handleWindowSizeChange}>
                                    <MenuItem value={0}>Tam Ekran</MenuItem>
                                    <MenuItem value={1}>Pencere Modu</MenuItem>
                                </Select>
                            </FormControl>
                        </Item>
                    </Grid>
                    <Grid item xs={6}>
                        <Item>
                            <FormControl fullWidth>
                                <InputLabel sx={{ color: "#fff !important" }} id="version-select-label">
                                    Versiyon
                                </InputLabel>
                                <Select sx={{ color: "#fff !important" }} labelId="version-select-label" id="version-select" value={version} label="Version" onChange={handleVersionChange}>
                                    <MenuItem value={0}>Riva Client</MenuItem>
                                </Select>
                            </FormControl>
                        </Item>
                    </Grid>
                </Grid>
            </Box>
        </div>
    );
}
